<template>
  <div>
    <el-row>
      <el-col class="mb20">
        <el-card>
          <el-form :model="options" ref="options" label-width="100px" class="vertical-form">
            <el-tabs value="basic">
              <el-tab-pane label="站点信息" name="basic">
                <el-form-item label="站点标题">
                  <el-input v-model="options.site_title"></el-input>
                </el-form-item>
                <el-form-item label="站点副标题">
                  <el-input v-model="options.site_sub_title"></el-input>
                </el-form-item>
                <el-form-item label="站点地址">
                  <el-input v-model="options.site_url"></el-input>
                </el-form-item>
                <el-form-item label="站点LOGO">
                  <el-upload
                    action="/api/admin/upload/image"
                    :on-remove="handleRemove"
                    :on-success="onSuccess"
                    :file-list="logoImg"
                    :limit="1"
                    list-type="picture"
                  >
                    <el-button size="small" type="primary">点击上传</el-button>
                    <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
                  </el-upload>
                </el-form-item>
                <el-form-item label="备案号">
                  <el-input v-model="options.site_icp"></el-input>
                </el-form-item>
                <el-form-item label="关键字">
                  <el-input v-model="options.keywords"></el-input>
                </el-form-item>
                <el-form-item label="站点描述">
                  <el-input type="textarea" :rows="3" v-model="options.description"></el-input>
                </el-form-item>
                <el-form-item label="统计代码">
                  <el-input type="textarea" :rows="5" v-model="options.statistic_code"></el-input>
                </el-form-item>
                <el-form-item label="推送代码">
                  <el-input type="textarea" :rows="5" v-model="options.push_code"></el-input>
                </el-form-item>
                <el-form-item label="页脚信息">
                  <el-input type="textarea" :rows="5" v-model="options.page_footer"></el-input>
                </el-form-item>
              </el-tab-pane>
              <el-tab-pane label="企业信息" name="company">
                <el-form-item label="企业名称">
                  <el-input v-model="options.company_name"></el-input>
                </el-form-item>
                <el-form-item label="企业地址">
                  <el-input v-model="options.company_address"></el-input>
                </el-form-item>
                <el-form-item label="邮政编码">
                  <el-input v-model="options.company_postcode"></el-input>
                </el-form-item>
                <el-form-item label="联系人">
                  <el-input v-model="options.company_contact"></el-input>
                </el-form-item>
                <el-form-item label="手机号">
                  <el-input v-model="options.company_mobile"></el-input>
                </el-form-item>
                <el-form-item label="联系电话">
                  <el-input v-model="options.company_phone"></el-input>
                </el-form-item>
                <el-form-item label="传真号码">
                  <el-input v-model="options.company_fax"></el-input>
                </el-form-item>
                <el-form-item label="电子邮箱">
                  <el-input v-model="options.company_email"></el-input>
                </el-form-item>
                <el-form-item label="联系QQ">
                  <el-input v-model="options.company_QQ"></el-input>
                </el-form-item>
                <el-form-item label="微信图标">
                  <el-upload
                    action="/api/admin/upload/image"
                    :on-remove="handleWechatRemove"
                    :on-success="onWechatSuccess"
                    :file-list="wechatImg"
                    :limit="1"
                    list-type="picture"
                  >
                    <el-button size="small" type="primary">点击上传</el-button>
                    <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
                  </el-upload>
                </el-form-item>
                <el-form-item label="营业执照">
                  <el-input v-model="options.company_blicense"></el-input>
                </el-form-item>
                <el-form-item label="其他信息">
                  <el-input v-model="options.company_other"></el-input>
                </el-form-item>
              </el-tab-pane>
              <el-tab-pane label="其他设置" name="other">
                <el-form-item label="全局head">
                  <el-input type="textarea" :rows="10" v-model="options.global_head"></el-input>
                </el-form-item>
                <el-form-item label="内容页head">
                  <el-input type="textarea" :rows="10" v-model="options.content_head"></el-input>
                </el-form-item>
              </el-tab-pane>
            </el-tabs>
            <el-form-item>
              <el-button type="primary" @click="submitForm()">保存</el-button>
              <el-button @click="resetForm()">重置</el-button>
            </el-form-item>
          </el-form>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import optionsApi from "@/api/optionsApi";
export default {
  data() {
    return {
      options: {},
      logoImg: [],
      wechatImg: []
    };
  },
  created() {
    this.getOptions();
  },
  methods: {
    getOptions() {
      optionsApi.getOptions().then(res => {
        this.options = res.data;
        let logo = this.options.site_logo;
        this.logoImg = [{ name: logo.split("@@")[1], url: logo }];
        let wechat = this.options.company_wechat;
        this.wechatImg = [{ name: wechat.split("@@")[1], url: logo }];
      });
    },
    submitForm() {
      optionsApi.save(this.options).then(res => {
        this.$message.success("保存成功");
      });
    },
    resetForm() {},
    onSuccess(res, file, fileList) {
      this.options.site_logo = res.data.url;
    },
    handleRemove(file, fileList) {
      this.options.site_logo = "";
    },
    onWechatSuccess(res, file, fileList) {
      this.options.company_wechat = res.data.url;
    },
    handleWechatRemove(file, fileList) {
      this.options.company_wechat = "";
    }
  }
};
</script>

<style lang="scss" scoped>
.vertical-form {
  .line {
    text-align: center;
  }
}
</style>>
