import request from '@/utils/request'

const baseUrl = '/api/admin/options'

const optionsApi = {}

optionsApi.findAll = (params) => {
  return request({
    url: `${baseUrl}`,
    params: params,
    method: 'get'
  })
}

optionsApi.getOptions = () => {
  return request({
    url: `${baseUrl}/all`,
    method: 'get'
  })
}

optionsApi.page = (params) => {
  return request({
    url: `${baseUrl}/page`,
    params: params,
    method: 'get'
  })
}

optionsApi.get = optionsId => {
  return request({
    url: `${baseUrl}/${optionsId}`,
    method: 'get'
  })
}

optionsApi.save = (params) => {
  return request({
    url: `${baseUrl}/save`,
    data: params,
    method: 'put'
  })
}

optionsApi.create = options => {
  return request({
    url: baseUrl,
    data: options,
    method: 'post'
  })
}

optionsApi.delete = optionsId => {
  return request({
    url: `${baseUrl}/${optionsId}`,
    method: 'delete'
  })
}

export default optionsApi
